.accordion {
  & summary {
    position: relative;
    padding: 20px 15px;
    margin: 0 -15px;
    font-size: 15px;
    line-height: 20px;
    background-color: #f6f6f6;
    cursor: pointer;

    &:hover {
      background-color: var(--light-gray);
    }
  }

  & summary::marker {
    content: none;
  }

  & summary::after {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
    content: '';
    width: 20px;
    height: 20px;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
  }

  & [open] summary::after {
    background-image: url(data:image/svg+xml;base64,);
  }

  & h2,
  h3 {
    width: calc(100% - 20px);
    font-size: 15px;
    font-weight: 400;
  }

  & ul {
    margin: 0 -15px;
    padding: 10px 15px 15px;
    font-size: 14px;
    background-color: #f6f6f6;

    & li:not(:last-child) {
      margin-bottom: 5px;
    }

    & a {
      display: inline-block;
      padding: 5px 0;
      color: var(--basic-green);
    }
  }

  @media (min-width: @tablet-width) {
    & summary {
      margin: 0;
      background-color: #f6f6f6;
      border-radius: 5px;
    }

    & ul {
      margin: 0;
      padding-left: 23px;
      border-radius: 0 0 5px 5px;
    }
  }
}
