.subway {
  font-size: 15px;
  color: #666666;

  & a {
    padding: 5px 0;
  }
}

.subway__distance {
  position: relative;
  padding-left: 20px;

  &::before {
    position: absolute;
    content: '';
    top: -1px;
    left: 0;
    width: 18px;
    height: 18px;
  }

  &--foot::before {
    background-image: url('../img/icons/walk_man.svg');
  }

  &--bus::before {
    background-image: url('../img/icons/bus.svg');
  }
}

.subway__icon {
  fill: #ff0000;
}

@media (min-width: @desktop-width) {
  .subway {
    font-size: 14px;
  }
}
