.card-equipment {
  & ul {
    display: grid;
    gap: 3px 0;
  }

  & li {
    padding-left: 40px;
    font-size: 14px;
    line-height: 35px;
    background-repeat: no-repeat;
  }
}

@media (min-width: 550px) {
  .card-equipment {
    & ul {
      grid-template-columns: repeat(2, 1fr);
      gap: 5px 10px;
    }
  }
}

@media (min-width: @tablet-width) {
  .card-equipment {
    & ul {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

